
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Popup from 'vux/src/components/popup/index.vue'


export default {
components: {
XButton,
Group,
XInput,
Selector,
Popup,
},
props: ['isShow'],
data () {
return {
title: '申请圈舍',
show: this.isShow,
form: {
name: '',
factory_status: '',
type: '',
farm_mode: '',
sealing: '',
sewage: '',
fan_mode: '',
length: '',
width: '',
fan_num: '',
cooling_pad_num: '',
area: '',
animal_num: '',
province: '',
city: '',
district: '',
address: '',
remark: '',
},
option: {
type: [{key:1, value: '无自动'}, {key:2, value: '半自动'}, {key:3, value: '全自动'}],
farm_mode: [{key:1, value: '散养'}, {key:2, value: '限位栏'}],
sealing: [{key:0, value: '无'}, {key:1, value: '有'}],
sewage: [{key:0, value: '无'}, {key:1, value: '有'}],
fan_mode: [{key:0, value: '无'}, {key:1, value: '有'}],
}
}
},
methods: {
add: function() {
this.$emit('close');

}
},
watch: {
isShow (val) {
this.show = this.isShow;
}
}
}



